        /* ---- grid toggle ---- */

        .grid-toggles {
            background: nth($scheme, 2);
            width: 100%;
            height: auto;
            position: sticky;
            pointer-events: none;
            // display: block;
            // display:none;
            z-index: 29; //review
            text-align: center;
            border-bottom: 1px solid;
            border-color: nth($scheme, 3);
            @include fluid-type(
                height,
                map-get($breakpoints, xs),
                map-get($breakpoints,xxl),
                map-get($lineheight, xs) * 2,
                map-get($lineheight,xxl) * 2
            );
            @include fluid-type(
                line-height,
                map-get($breakpoints, xs),
                map-get($breakpoints, xxl),
                map-get($lineheight, xs) * 2,
                map-get($lineheight,xxl) * 2
            );
            @include fluid-type(
                top,
                map-get($breakpoints, xs),
                map-get($breakpoints,xxl),
                map-get($lineheight, xs) * 2.5,
                map-get($lineheight,xxl) * 2.5
            );

            @media (min-width: map-get($breakpoints, md)) {
                border-bottom: none;
                background: transparent;
                height: 100%;
                position: absolute;
                top: 0;
                bottom: 0;
            }
            @media (min-width: map-get($breakpoints, lg)) {
                display: block;
            }

            .toggle-list {
                .grid-toggle {
                    pointer-events: initial;
                    display: block;
                    width: 100%;
                    cursor: pointer;
                    position: relative;
                    // width: fit-content;
                    margin: auto;
                    // @include fluid-type(top, map-get($breakpoints, xs), map-get($breakpoints,xxl), map-get($lineheight, xs)*2, map-get($lineheight,xxl)*2);

                    &.active-layer {
                        // background-color: red;
                    }
                } //.grid-toggle

                @media (max-width: map-get($breakpoints, md)) {
                margin-top: 0px!important;//ensure no offets applied on mobile
                }

                @media (min-width: map-get($breakpoints, md)) {
                    position: fixed;
                    top: 50%;
                    width: 100%;

                    .grid-toggle {
                        width: initial;

                        @include fluid-type(
                            font-size,
                            map-get($breakpoints, xs),
                            map-get($breakpoints,xxl),
                            map-get($fontsize, xs) * 2,
                            map-get($fontsize,xxl) * 2
                        );
                        @include fluid-type(
                            line-height,
                            map-get($breakpoints, xs),
                            map-get($breakpoints,xxl),
                            map-get($lineheight, xs) * 2,
                            map-get($lineheight,xxl) * 2
                        );

                        &.active-layer {
                            //  -color: red;
                        }
                    } //.grid-toggle
                } //   @media (min-width: map-get($breakpoints, lg))
            } //.toggle-list

            &.toggles-active {
                .toggle-button {
                    display: none;
                }
                .toggle-list {
                    .grid-toggle {
                        pointer-events: initial;
                        //  width: 100%;
                        position: relative;
                        display: none;

                        &.active-layer {
                            // background-color: green;
                            display: inline-block;
                        }
                    } //.grid-toggle
                } //.toggle-list
            } //  &.toggles-active

            &.toggles-active.toggles-on,
            &.toggles-on {
                bottom: 0;
                height: var(--site-height);

                @media (min-width: map-get($breakpoints, lg)) {
                height: auto;
                }

                // top:0px;
                display: flex;
                flex-direction: column;
                position: fixed; //review
                border-bottom:0px;

                .toggle-button {
                    // display: none;
                }
                .toggle-list {
                    height: inherit;

                    display: block;
                    position: relative;
                    overflow: hidden;

                    .grid-toggle {
                        pointer-events: initial;
                        display: block;
                        position: relative;
                        clear: both;
                        width: fit-content;
                        position: relative;

                        @media (max-width: map-get($breakpoints, md)) {
                            border-bottom: 1px solid;
                            border-color: nth($scheme, 3);
                            width: 100%;
                            height: 33.33%;
                            line-height:calc(var(--site-height) / 3);
                            //33.33vh;
                            @include fluid-type(
                                font-size,
                                map-get($breakpoints, xs),
                                map-get($breakpoints,xxl),
                                map-get($fontsize, xs) * 2,
